import React from 'react';
import ReactDOM from 'react-dom';
import $ from 'jquery';
import {Link} from 'react-router';

/**
 * The sidebar component
 */
export default class Sidebar extends React.Component {

  componentDidMount() {
    $('html').addClass('html--sidebar__collapsed')

    $('.sidebar--nav--item__has-submenu>a').each(function(index, el) {
      var parentUl = $(this).parent().parent()
      var slideInUl = $(this).next('ul')
      var parentText = $(this).text()

      $(
       `<li class="sidebar--nav--submenu--back-button">
          <a href="#">
            <i class="icon__fa icon--chevron-left"></i>
            <span>${parentText}</span>
          </a>
        </li>`)
      .prependTo(slideInUl)
      .click(function(e) {
        e.preventDefault()
        parentUl.removeClass('slide-in')
        setTimeout(function() {
          parentUl.find('ul').css({visibility: 'hidden'})
        }, 300);
      })

      $(this).on('click', function(e) {
        e.preventDefault()
        $(this).next('ul').css({visibility: 'visible'})
        parentUl.addClass('slide-in')
      })
    })
  }

  render() {
    return (
      <aside className="sidebar sidebar__fixed background__white">
        <div className="sidebar--header">
          <div id="user-info" className="notification">
            <figure>
              <img src="images/avatar.png"></img>
            </figure>
            <div className="user-info">
              <b><Link to="profile">User Name</Link></b><br/>
              <span>User Role</span>
            </div>
          </div>
        </div>
        <div className="sidebar--scroll-container">
          <ul className="sidebar--nav">
            {/* menu items here */}
          </ul>
        </div>
      </aside>
    )
  }
}

