import React from 'react';
import $ from 'jquery';

var appTitle = "Application Title"


// custom inline styles
var styles = {
  navbar: {
    minWidth: '400px'
  },

  searchBox: {
    verticalAlign: 'middle',
    display: 'inline-block',
    marginRight: '2em'
  },

  appTitle: {
    whiteSpace: 'nowrap'
  }
}


// navbar
export default class Nav extends React.Component {
  onSidebarToggle(e) {
    e.preventDefault()
    $('html').toggleClass('html--sidebar__collapsed')
  }

  render() {
    return (
      <nav className="navbar-main navbar-main__fixed background__white" style={styles.navbar}>
        <div className="navbar-main--segment__left">
          <div className="navbar-main--item navbar-main--item__icon">
            <a id="sidebar-toggle" href="#" onClick={this.onSidebarToggle}><i className="icon__fa icon--bars type-size__medium"></i></a>
          </div>
          <div className="navbar-main--item navbar-main--item__logo">
            <h2><a style={styles.appTitle} href="#">{appTitle}</a></h2>
          </div>
        </div>
        <div className="navbar-main--segment navbar-main--segment__right">
          <div className="navbar-main--item navbar-main--item__no-border-effect hide-s">
            <div className="form--field form--field__isolated" style={styles.searchBox}>
              <div className="form--input-group">
                <input type="search"></input>
                <a href="#" className="button button__icon">
                  <i className="icon__fa icon--search"></i>
                </a>
              </div>
            </div>
          </div>
        </div>
      </nav>
    )
  }
}



