import React, { PropTypes } from 'react'

import { bindActionCreators } from 'redux'
import { connect, Provider } from 'react-redux';
import * as Actions from '../actions/actions';

@connect(
  (state) => ({ demoString: state.demo }), // map state to props
  (dispatch) => ({ actions: bindActionCreators(Actions, dispatch)}) //map dispatch to props
)
export default class DemoComponent extends React.Component {
  componentDidMount() {
    console.log(this)
  }

  render () {
    return (
      <section key={1} className="padd__navbar-main__top">
        <div className="row">
          <div className="span-l12">
            <h1>{this.props.demoString}</h1>
          </div>
        </div>
      </section>
    )
  }
}



